#openBtn{
    position: relative;
    left: 186px;
    border: none;
    bottom: 65px;
    color: rgb(255, 255, 255);
    -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
            clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    height: 25px;
    background: linear-gradient( rgb(142, 71, 255), rgb(204, 247, 255)  );
}
#main .icons{
    width: 22px;
    height: 17px;
    color:rgb(155, 226, 216);
}


@keyframes border {
    0% {
      border: rgb(93, 235, 204) 0px solid;
    }
    20% {
        border: rgb(93, 235, 204) 2px solid;
    }
    40%
    {
        border: rgb(93, 235, 204) 4px solid;
    }
    60%
    {
        border: rgb(93, 235, 204) 6px solid;
    }
    80%
    {
        border: rgb(93, 235, 204) 4px solid;
    } 90%
    {
        box-shadow: rgb(93, 235, 204) 20px 15px 20px ;
    }
    99%
    {
        box-shadow: rgb(93, 235, 204) 20px 15px 20px ;
    }
}
@media only screen and (max-width: 1255px) {
